@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff2") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
.main-container {
  flex-grow: 1;
}
.page {
  height: 100%;
  background-color: #ffffff;
}
.page__body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  color: hwb(0 27% 73%);
}
.main-header {
  background-color: hsl(0, 0%, 100%);
  padding: 0;
}
.main-header__note {
  display: none;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    width: 768px;
    margin: 0 auto;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    justify-content: space-between;
    grid-template-areas: "A . B B" "C C D D";
  }
  .main-header__logo {
    height: 79px;
    margin: 0 auto;
    grid-area: A;
    align-items: center;
    justify-self: start;
  }
  .main-header__toggle {
    display: none;
  }
  .main-header__note {
    padding-top: 20px;
    position: absolute;
    display: block;
    grid-area: D;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    text-align: right;
    color: #444444;
    z-index: 2;
    right: 0;
  }
}
@media (min-width: 1150px) {
  .main-header__wrapper {
    width: 1150px;
    margin: 0 auto;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    width: 1050px;
    justify-content: space-between;
    grid-template-areas: "C C A A B B" "C C . . D D";
  }
  .main-header__toggle {
    display: none;
  }
}
.main-nav {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: 1150px;
  position: relative;
}
.main-nav__header-wrapper {
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  border-bottom: 1px solid hwb(0 93% 7%);
}
.main-nav__list {
  color: hwb(0 0% 100%);
  text-decoration: none;
}
.main-nav__header-logo {
  display: flex;
  justify-content: center;
}
.main-nav__logo-image {
  padding: 20px;
}
.main-nav__logo-image:hover {
  opacity: 0.6;
}
.main-nav__logo-image:active {
  opacity: 0.3;
}
.main-nav__toggle {
  position: absolute;
  cursor: pointer;
  right: 31px;
  top: 32px;
  width: 21px;
  height: 21px;
  padding: 0;
  border: 0;
  background-color: transparent;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 2px;
  background-color: hwb(0 0% 100%);
  box-shadow: 0 6px 0 0 hwb(0 0% 100%), 0 12px 0 0 hwb(0 0% 100%);
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 9px;
  right: 0;
  width: 20px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .site-nav__item {
    display: none;
  }
  .main-nav--closed .user-nav__item {
    display: none;
  }
  .main-nav--opened .site-nav__item {
    display: block;
  }
  .main-nav--opened .user-nav__item {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .site-nav__item {
    display: block;
  }
  .main-nav--nojs .user-nav__item {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav {
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;
    width: 668px;
    margin: 0 auto;
    border-bottom: 1px solid #ececec;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "A B B B" "C C D D";
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__header-wrapper {
    display: flex;
    justify-content: start;
    grid-area: A;
    border-bottom: none;
  }
  .main-nav__logo-image {
    display: block;
    padding-top: 22px;
    padding-bottom: 20px;
    padding-left: 0;
  }
}
@media (min-width: 1150px) {
