.filter__mark {
  position: absolute;
  width: 36px;
  height: 36px;
  border: 2px solid #444444;
  background: transparent;
  top: -6px;
  left: 0;
}
.filter__mark--second {
  width: 34px;
  height: 34px;
  top: -7px;
}
